
import React, { Component } from 'react'



class SearchBar  extends Component {
   //uncontrolMethod/{onInputChange(event){console.log(event.target.value);}}
   constructor(){
      super();
     this.state = { term:''}  
   };

   onEventSubmit = (event) =>{

       event.preventDefault();
       //when you use preventDefualt then form do not submit automatically

        this.props.onSubmit(this.state.term)
           
   }
    
  render() {
    return (
      <div className=" ui segment">
       
        <form onSubmit={this.onEventSubmit} className='ui form'>  
              <div className='ui search'> 
            <label className='editImages'> Images search..</label>
                    <input 
                      type="text"
                      placeholder="search anything..."
                        value={this.state.term} //passing props
                      //onChange ={this.onInputChange} //uncontrolMethod
                       onChange = {(e)=>this.setState({term:e.target.value})} 
                       />

                      
             </div> 
        </form>
      </div>
    );
  }
}

export default SearchBar ;

